:root {
  --primary_color: white;
  --secondary_color: #161a1d;
  --box_shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}

.draggable-container {
  /* width: 200px;
  height: 200px; */
  top: "35%";
  left: "30%";
  transform: "translate(-50%, -50%)";
  /* background-color: #161a1d; */
  background-color: var(--primary_color);
  box-shadow: var(--box_shadow);
  border-radius: 3px;
  padding-left: 2px;
  padding-right: 2px;
  /* display: flex; */
  justify-content: center;
  align-items: center;
  transition: opacity 0.3s ease-in-out;
  opacity: 1;
  cursor: grab;
  /* position: fixed; */
  position: relative;
  z-index: 9999;
  padding: 5px 10px;
}

.draggable-container.closed {
  opacity: 0;
}

.draggable-container:not(.closed) {
  cursor: grab;
}

.draggable-container:not(.closed):hover .draggable-button {
  cursor: grab;
}

.draggable-container.closed,
.draggable-container:not(.closed):hover {
  cursor: default;
}

/* SearchBar.css */
.search-bar {
  position: relative;
  display: inline-block;
  width: 500px;
  margin: 1px auto;
}

input[type="text"] {
  margin: auto;
  width: 95%;
  height: 100%;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 16px;
  border: none;
  outline: none;
  cursor: text;
}

/* input[type="text"]:focus {
  border-color: balck;
  box-shadow: 0 0 5px #000000;
} */

.search-bar-input {
  display: flex;
  align-items: center;
  padding: 5px 15px 5px 15px;
  border-radius: 5px;
  box-shadow: var(--box_shadow);
  background-color: var(--primary_color);
}

.search-icon {
  color: var(--secondary_color);
  margin-right: 5px;
}

.mic-icon {
  margin-left: 5px;
  padding: 5px;
  cursor: pointer;
  color: var(--secondary_color);
  transition: transform 0.3s ease;
}

.mic-icon:hover {
  color: var(--secondary_color);
  transform: translateY(-3px);
}

.loading-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--secondary_color);
  height: 30px;
}

/* Mic Button isListning CSS */
.listening-message {
  position: absolute;
  right: 40px;
  top: 20px;
  transform: translateY(-50%);
  background-color: var(--secondary_color);
  color: var(--primary_color);
  padding: 5px 10px;
  border-radius: 4px;
}

.mic-icon.listening {
  color: var(--secondary_color);
  animation: bounce 1s infinite alternate;
}

@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-5px);
  }
}

/* Search result */
.search-results {
  color: var(--secondary_color);
  box-shadow: var(--box_shadow);
  padding: 20px;
  margin-top: 10px;
}

/* Top Bar  */
.top-bar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
}

.cross-icon {
  cursor: pointer;
  font-size: 20px;
  color: var(--secondary_color);
  align-self: flex-end;
  padding-right: 10px;
}

/* No Results */
.round-border {
  border: 1.5px solid #ccc;
  border-radius: 10px;
  padding: 20px;
  margin-top: 10px;
}

.no-results-message {
  text-align: center;
  color: var(--secondary_color);
  box-shadow: var(--box_shadow);
  padding: 20px;
  margin-top: 10px;
}

/* Styles for the search default sentences container */
.search-default-sentences {
  display: flex;
  flex-direction: column;
  background-color: var(--primary_color);
  box-shadow: var(--box_shadow);
  border-radius: 3px;
  margin-top: 5px;
}

.search-default-sentences div {
  padding: 8px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.search-default-sentences div:hover {
  background-color: #eaeaea;
}

/*backdrop style */
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  /* z-index lower the draggable container to make it below the container */
  z-index: 9998;
  display: none;
}

.blur {
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  display: block;
}
